/* General Reset */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
body {

  min-height: 120vh;
  font-family: 'Poppins', sans-serif; /* Modern font */
}
.table-container {
  background: white;
  border-radius: 12px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

/* Content Section */
.content {
  height: 120vh;
  padding: 20px;
}

/* Navbar */
.navbar {
  background-color: #e0f7fa !important;
}

/* Filters Section */
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px rgb(205, 193, 170);
  background: #fff;
  border-radius: 5px;
}

/* Switch Icons */
.switch-icons {
  display: flex;
  align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.6);
  border-radius: 5px;
  padding: 10px 15px;
  gap: 10px;
}

.anticon svg {
  font-size: 20px;
  cursor: pointer;
  transition: color 0.3s ease-in-out;
}
.ant-table-tbody > tr:hover {
  background-color: #268cbf !important;
  transition: 0.3s ease-in-out;
}

.active-icon {
  color: black;
}

.inactive-icon {
  color: gray;
}

/* Modal Styling */
.ant-modal-title {
  font-size: 20px;
  font-weight: bold;
  color: #333;
}

/* Transaction Form */
.ant-form-item-label label {
  font-weight: 600;
  color: #333;
}

/* Input Fields */
.ant-input,
.ant-select-selector {
  border: 2px solid #ced4da;
  border-radius: 5px;
  padding: 10px;
  transition: all 0.3s ease-in-out;
}

.ant-input:focus,
.ant-select-focused .ant-select-selector {
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}
.ant-table {
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
}

/* Modal Buttons */
.d-flex.justify-content-end .btn-primary {
  background: #007bff;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 5px;
  color: white;
  font-weight: bold;
}

.d-flex.justify-content-end .btn-primary:hover {
  background: #0056b3;
}

/* Responsive Design */
@media (max-width: 768px) {
  .filters {
      flex-direction: column;
      gap: 10px;
      align-items: flex-start;
  }
}

/* Login Page */
.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url('/public/hostel.jpeg'); /* Ensure the image is in the public folder */
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
}

/* Overlay for better readability */
.login-page::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 0;
}

/* Login Form */
.login-page form {
  background: rgba(255, 255, 255, 0.95);
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
  text-align: center;
  border: 1px solid #ddd;
  position: relative;
  z-index: 1;
}

/* Form Labels */
.login-page .ant-form-item-label label {
  color: #333;
  font-weight: 600;
}

/* Input Fields */
.login-page .ant-input {
  background: #ffffff;
  border: 2px solid #ced4da;
  color: #495057;
  padding: 10px;
  border-radius: 5px;
  transition: border-color 0.3s ease-in-out;
}

.login-page .ant-input:focus {
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

/* Button */
.login-page .btn-primary {
  background: #007bff;
  border: none;
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
  border-radius: 5px;
  color: white;
  font-weight: bold;
}

.login-page .btn-primary:hover {
  background: #0056b3;
}

/* Link */
.login-page a {
  color: #007bff;
  text-decoration: none;
  font-size: 14px;
}

.login-page a:hover {
  text-decoration: underline;
}

