.add-button {
  background: linear-gradient(45deg, #007bff, #0056b3);
  color: white;
  padding: 10px 20px;
  border-radius: 8px;
  transition: all 0.3s ease-in-out;
}

.add-button:hover {
  box-shadow: 0px 4px 15px rgba(0, 123, 255, 0.5);
  transform: scale(1.05);
}
.ant-message {
  z-index: 9999 !important;
}
