/* General Styles */
.analytics-container {
  padding: 20px;
}

.card {
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease-in-out;
}

.card:hover {
  transform: scale(1.02);
}

.card-header {
  font-weight: bold;
  text-align: center;
  background-color: #f8f9fa;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.card-body {
  text-align: center;
  padding: 15px;
}

.text-success {
  color: #28a745;
  font-weight: bold;
}

.text-danger {
  color: #dc3545;
  font-weight: bold;
}

/* Progress Bars */
.ant-progress-circle {
  margin: 10px;
}

/* Category Cards */
.category-card {
  margin-bottom: 10px;
  border-left: 5px solid #17a2b8;
}

.category-card .card-body {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Responsive Grid */
@media (max-width: 768px) {
  .row {
    flex-direction: column;
  }

  .col-md-4 {
    width: 100%;
    margin-bottom: 15px;
  }
}
.category-heading {
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  color: #343a40;
  border-left: 5px solid #17a2b8;
  padding-left: 10px;
  margin-bottom: 10px;
  display: inline-block;
}


